#change:hover{
  color: dimgray;
  text-decoration: underline;
  cursor: pointer;
}

#labels {
  overflow: hidden;/* Makes this div contain its floats */
}

#logout{
  float: left;
  text-align: left;
  font-size: medium;
  margin-top: 10px;
}

.lineC{
  direction: rtl;
  display:flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 3px;
  margin-bottom: 3px;
}

#logout:hover{
  color: blue;
  text-decoration: underline;
  cursor: pointer;
}
.App-header {
  background-color: white;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
}

.App-link {
  color: #61dafb;
}


.cardApp{
  font-size: small;
  text-align: right;
  /*margin: 0 25% 3% 2%;*/
  cursor: pointer;
  width: 80%;
  margin: auto
}

.popupApp {
  float: right;
  text-align: right;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 0.5);
}

.popup_inner_App {

  position: absolute;
  left: 35%;
  right: 35%;
  top: 25%;
  bottom: 35%;
  margin: auto;
  background: white;
}

#pass, #pass2{
  width: 40%;
}

#h3{
  text-align: center;
}

#x {
  color: rgb(180, 180, 180);
  font-size: large;
  font-family: sans-serif;
  float: right;
  text-align: right;
  background-color: white;
  font-weight: bolder;
  width: fit-content;
  margin-top: 0;
}

#diffPass{
  font-weight: bold;
}